.seeMore{
    
    height: 50px;
    width: 50px;
    background-color: black;
    border-radius: 25px;
    margin-bottom: 10px;
    margin-right: 10px;
    margin-top: 10px;
    align-items: center;
    justify-content: center;
    box-shadow: 2px 2px 3px #999;

}

.seeMore img{
    margin-top: 10px;
    margin-left: 10px;
    width: 30px;
    height: 30px;
}

.seeMore:hover{
    cursor: pointer;
}